/* Base CSS */

@font-face {
  font-family: RusticLoveTattoo;
  src: url(../fonts/RusticLoveTattoo.otf);
}

.alignleft {
  float: left;
  margin-right: 15px;
}

.alignright {
  float: right;
  margin-left: 15px;
}

.aligncenter {
  display: block;
  margin: 0 auto 15px;
}

a:focus {
  outline: 0 solid;
}

a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h3 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h4 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h5 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h6 {
  margin: 0 0 15px;
  color: #1B1B21;
}

body {
  color: #545051;
  font-family: "Open Sans", sans-serif;
}

.hero {
  padding-top: 2em;
  padding-bottom: 2em;
}

.hero .logo-container {
  position: absolute;
  top: 50px;
  left: 50px;
  transform: rotate(-8deg);
}

.hero .logo-container img {
  width: 150px;
}

.hero .logo-container p {
  color: #fff;
  font-size: 13pt;
  font-family: "Kalam", cursive;
}

.hero .hero-container {
  max-width: none;
}

.hero .hero-container .heading {
  font-size: 65px;
}

.hero .hero-container .lead {
  margin-top: 12pt;
  font-size: 15pt;
  color: #f1f1f1;
}

.section-title {
  text-align: center;
  margin-bottom: 42px;
}

.section-title h2 {
  color: #d0063c;
  font-size: 38px;
  font-weight: bold;
}

.section-title p {
  font-size: 13pt;
}

.fids-list {
  padding-top: 30px;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.fids-list .accent-arrow {
  position: absolute;
  left: -70px;
  top: 50px;
}

.fids-list .single-stage-list h2 {
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 4px;
  margin-top: 10px;
}

.fids-list .single-stage-list p {
  font-size: 15px;
  color: #fff;
  margin: 0;
}

.video-area {
  padding: 17px 0;
  color: #666;
  position: relative;
  font-weight: 500;
}

.video-area iframe-phone {
  border-radius: 20px;
  position: absolute;
  top: 14px;
  left: 55px;
  width: 430px;
  height: 198px;
}

.video-area .video-main-wrp {
  margin-bottom: 38px;
}

.video-area .video-blk-inner {
  max-width: 830px;
  margin: 0 auto;
  color: #495057;
  font-size: 20px;
}

.video-area .accent {
  position: absolute;
  bottom: 0px;
  right: -70px;
  z-index: -1;
}

.help-area {
  background: #0096db;
  padding: 50px 0;
}

.help-area .section-title {
  color: #fff;
  margin-bottom: 0;
}

.help-area .section-title h2 {
  color: #fff;
}

.help-area .owl-nav button {
  position: absolute;
  left: -60px;
  top: 45%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.help-area .owl-nav button img {
  max-width: 32px;
}

.help-area .owl-nav button.owl-next {
  left: auto;
  right: -60px;
}

.help-area .hlp-img {
  margin-bottom: 19px;
}

.help-area .hlp-img img {
  max-width: 150px;
  margin: 0 auto;
}

.help-area .single-help-slider-blk {
  text-align: center;
  color: #fff;
  position: relative;
  margin-top: 40px;
}

.help-area .single-help-slider-blk h3 {
  color: #fff;
  font-size: 17px;
  font-weight: 700;
  margin-top: 12px;
  margin-bottom: 9px;
}

.help-area .single-help-slider-blk p {
  max-width: 300px;
  margin: 0 auto;
  margin-bottom: 18px;
}

.help-area .single-help-slider-blk a {
  color: #fff;
  font-size: 16px;
}

.help-area .single-help-slider-blk a i {
  margin-right: 6px;
}

.step-area {
  padding: 70px 0;
}

.step-area .row.single-row {
  max-width: 88%;
  margin: 0 auto;
}

.step-area .step-text-blk {
  padding: 30px;
  border: 2px solid #0096db;
  background: #fff;
  width: 530px;
  position: relative;
}

.step-area .step-text-blk p {
  font-size: 16px;
  color: #495057;
  line-height: 30px;
}

.step-area .step-text-blk p span {
  font-weight: 600;
  font-size: 20px;
}

.step-area .step-text-blk p a {
  color: #ed1651;
}

.step-area .step-text-blk a {
  color: #0096db;
  font-size: 15px;
  font-weight: 600;
}

.step-area .step-text-blk a i {
  margin-right: 7px;
}

.step-area .step-blk-top {
  position: relative;
  padding-left: 82px;
  margin-bottom: 42px;
}

.step-area .step-blk-top h2 {
  font-size: 40px;
  text-transform: uppercase;
  font-weight: 900;
  color: #495057;
}

.step-area .step-blk-top h2 span {
  display: block;
  font-size: 20px;
  font-weight: 400;
  text-transform: none;
}

.step-area .step-img {
  position: absolute;
  left: 0;
  top: 4px;
  max-width: 67px;
}

.step-area .row.step-1 {
  margin-top: 40px;
}

.step-area .step-img-main {
  position: relative;
}

.step-area .step-img-main img.br-1 {
  max-width: 350px;
  position: absolute;
  right: -211px;
  z-index: -1;
  top: 6px;
}

.step-area .step-2 .step-text-blk {
  margin: 0 auto;
  display: block;
}

.step-area .row.single-row.step-2 {
  max-width: 100%;
  width: 100%;
  margin-top: 70px;
}

.step-area .br-2 {
  z-index: -1;
  position: absolute;
  right: -58px;
  top: -138px;
}

.step-area .do-list-wrp {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.step-area .do-list-wrp .do-list + .do-list {
  text-align: right;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 60%;
  flex: 0 0 60%;
  max-width: 60%;
}

.step-area .do-list {
  max-width: 40%;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 50%;
  flex: 0 0 40%;
}

.step-area .do-list h4 {
  font-size: 16px;
  font-weight: 400;
  color: #495057;
}

.step-area .do-list ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  margin-bottom: 21px;
}

.step-area .do-list ul li {
  color: #495057;
  font-size: 16px;
  position: relative;
  padding-left: 12px;
}

.step-area .do-list ul li:after {
  position: absolute;
  left: 0;
  top: 11px;
  width: 5px;
  height: 5px;
  background: #495057;
  content: "";
  border-radius: 50%;
}

.step-area .do-list img {
  max-width: 202px;
  display: block;
  margin-left: auto;
  margin-top: -40px;
}

.step-area .row.single-row.step-3 {
  margin-top: 142px;
}

.step-area .b-3 {
