  width: 100%;
  background: #f4f4f4;
  padding: 25px;
  position: relative;
  margin-bottom: 20px;
}

.step-area .do-list-wrp.tips-list .do-list {
  max-width: 100%;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
}

.step-area .do-list-wrp.tips-list h4 {
  color: #000000;
  font-size: 16px;
  font-weight: 700;
}

.step-area .do-list-wrp.tips-list ul li {
