  font-size: 24px;
  font-weight: 600;
  margin-bottom: 6px;
}

.subs-form form label {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
}

.subs-form form input {
  width: 257px;
  border: none;
  height: 40px;
  margin-left: 6px;
  padding: 6px;
}

.subs-form form button {
  height: 40px;
  color: #fff;
  background: #0096db;
  border: none;
  width: 114px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 20px;
  border-radius: 3px;
  margin-left: 6px;
}

.sp-2 {
